import { connect, keyStores, WalletConnection } from 'near-api-js';

async function connectToContract() {
  const near = await connect({
    networkId: 'testnet', // Specify the network ID (e.g., "testnet" or "mainnet")
    keyStore: new keyStores.BrowserLocalStorageKeyStore(),
    nodeUrl: 'https://rpc.testnet.near.org', // Specify the URL of the NEAR RPC node
  });

  const walletConnection = new WalletConnection(near, 'funder2'); // Specify your app name

  // Get the contract instance using the contract address
  const contract = await new near.Contract(
    walletConnection.account(),
    'emna.testnet', // Specify the contract address
    {
      // Specify additional options if needed
    }
  );

  // Interact with the contract methods
  // Interact with the contract methods
const result = await contract.get_greeting();
console.log(result);

}

connectToContract();

